[class^="icon-"], [class*=" icon-"] {
	// It's possible that a link or other element can get a font-family from elsewhere,
	// which can accidentally override this font-family if there are class collisions.
	// Adding the '!important' here makes the icon-* class take precedence over others.
	// Could be trouble down the road, but let's hope not.
	font-family: 'SimpleIcons' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;


    // icon fix for Windows firefox
	&::before {
		font-size: 1.04em;
	}
}

